.item{
    width: calc((100% - 30px * 1) / 1);
    flex-direction: column;
    display: flex;
    border: 1px solid black;
    padding: 10px;
    color: whitesmoke;
}
.item:nth-child(1){
    background-color: rgb(0, 188, 245);
}
.item:nth-child(2){
    background-color: rgb(250, 183, 0);
}
.item:nth-child(3){
    background-color: rgb(245, 0, 0);
}
.item:nth-child(4){
    background-color: rgb(55, 47, 124);
}
.item:nth-child(5){
    background-color: rgb(240, 82, 82);
}